import {createLevels} from "core/types"

export interface Level {
  itemCount: number
  hiddenItemCount: number
  wrongAnswerCount: number
  positionCount: number
}

function level(itemCount: number, hiddenItemCount: number, wrongAnswerCount: number, positionCount: number): Level {
  return {itemCount, hiddenItemCount, wrongAnswerCount, positionCount}
}

export const levels = createLevels({

  // 1: level(9,	6,	18,	14),
  // 1: level(9,	6,	6,	14),
  1: level(3, 1, 2, 5),
  2: level(3, 2, 2, 6),
  3: level(4, 2, 2, 7),
  4: level(4, 3, 3, 8),
  5: level(5, 3, 3, 9),
  6: level(6, 4, 4, 10),
  7: level(7, 4, 5, 11),
  8: level(8, 5, 5, 12),
  9: level(8, 6, 6, 13),
  10: level(9, 6, 6, 14)
})



