import {CoreSounds} from "core/assets"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn, Control} from "core/ui/controls"
import {ImageWithShadow} from "core/ui/controls/imgWithShadow"
import {screenSizeService} from "core/ui/services/instances"
import {RandomGenerator} from "core/utils"
import * as _ from "lodash"
import {Images} from "../images"

const durations = {
  click: 200,
  hover: 200
}

export class AnswersComponent extends GameComponent<number[]> implements OnInit {
  positions = Sizes.create({
    margin: {x: 50, y: 0},
    y: 175
  })

  positionsPortrait = Sizes.expand(this.positions, {
    margin: {x: 0, y: 0},
    y: 20
  })
  getPositions = () => screenSizeService.isPortrait ? this.positionsPortrait : this.positions

  images: Btn[] = []
  highlightTweens: Phaser.Tween[] = []

  group = this.add(new Control()).updateAnchor(0.5, screenSizeService.isPortrait ? 0 : 0.5)

  onInit(): void {
    this.onChangeForRun(() => screenSizeService.isPortrait, x => {
      this.updatePositions()
    })
  }

  updatePositions() {
    const colCount = screenSizeService.isPortrait ? 4 : 8

    Sizes.tableFromList(this.images, colCount, this.getPositions().margin)
    this.group.updatePivot()
    this.group.updatePos(Sizes.w2, this.getPositions().y)
  }

  highlightAnswers() {
    if (this.isTutorial) {
      this.showHighlight(this.images.filter(x => _.includes(this.gameState.answers.restAnswers, x.tag)).map(x => x.back!))
    }
  }

  showHighlight(controls: Control[]) {
    this.highlightTweens.forEach(x => x.stop(true))
    this.highlightTweens = controls.map(y => CommonAnimations.highlight(y))
  }

  rng = new RandomGenerator()

  clickAnimation(btn: Btn) {
    btn.symmetricScale = 1
    Tweens.start({
      target: btn,
      to: {symmetricScale: 1.33},
      duration: durations.click,
      simpleYoyo: true
    })
  }

  hoverAnimation(btn: Btn, reverse: boolean) {
    if (!reverse) {
      this.playSound(CoreSounds.hover, false, true)
    }
    Tweens.start({
      target: btn,
      to: {angle: btn.angle + (reverse ? -1 : 1) * 38},
      duration: durations.hover
    })
  }

  onPropsUpdate() {
    this.group.removeAll(true)
    this.images = this.props.map(item => {
        const btn = new Btn("", btn => {
            this.clickAnimation(btn)
            this.playerAnswer(btn.tag)
          }, undefined, new ImageWithShadow(this.gameKey(Images.item(item)))
        ).updateFields({tag: item})
        btn.onHover(x => this.hoverAnimation(btn, x))
        btn.symmetricScale = screenSizeService.isPortrait ? 0.8 : 1
        btn.anchorCenter()
        btn.angle = this.rng.int(360)

        return btn
      }
    )
    this.group.addChildren(this.images)
    this.updatePositions()
    this.highlightAnswers()
  }
}




