import {AnswerId, AnswerStatus, ILastAnswer} from "core/types/logic"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {Img} from "core/ui/controls"
import {RandomGenerator} from "core/utils"
import {Images} from "games/shop/images"
import {Shelf, shelfCount, shelfMaxPositionCount} from "games/shop/logic"
import * as _ from "lodash"

const emptyItem = -1

const rng = new RandomGenerator()

const books = _.range(shelfCount).map(x => rng.array(shelfMaxPositionCount, Images.bookCount))

export class RoomShelfComponent extends GameComponent<Shelf & { index: number }> {
  positions = Sizes.create({
    between: 22
  })

  rng = new RandomGenerator()
  images: Img[] = []
  imageSizes = Images.sizes()
  itemScale = this.imageSizes.itemSmall / this.imageSizes.item

  setVisibleForItems(items: AnswerId[], visible: boolean) {
    this.images.forEach(x => {
      if (_.includes(items, x.tag)) {
        x.visible = visible
      }
    })
  }

  setVisibleForAllItems(visible: boolean) {
    this.images.filter(x => x.tag != emptyItem).forEach(x => {
      x.visible = visible
    })
  }

  onAnswer(answer: ILastAnswer) {
    if (AnswerStatus.isCorrect(answer.status)) {
      this.setVisibleForItems([answer.answerId], true)
    }
  }

  getItemX(position: number) {
    return (this.positions.between + this.imageSizes.itemSmall) * position
  }

  onPropsUpdate() {
    this.removeAll(true)
    const items = this.props.items.map(x => ({
      position: x.position,
      item: x.item,
      key: Images.item(x.item)
    })).concat(_.range(shelfMaxPositionCount - this.props.length).map(i => {
      const position = this.props.length + i
      return {
        position,
        item: emptyItem,
        key: Images.book(books[this.props.index][position])
      }
    }))
    this.images = items
      .map(x => {
          const img = this.addImage([x.key, [this.getItemX(x.position), 0]]).updateFields({tag: x.item})
          img.symmetricScale = this.itemScale
          return img
        }
      )
  }
}




