import {ImagesMetaData} from "core/types"
import {Sizes} from "core/ui/common/sizes"
import * as _ from "lodash"
import {itemCount} from "./logic"

export module Images {
  const catCount = 4
  const flowCount = 4
  const fishJumpCount = 20
  const fishCount = 4
  const toktokCount = 17
  const buttCount = 3
  export const bookCount = 3
  export const sizes = () => Sizes.create({
    item: 120,
    itemSmall: 82,
    toktok: {h: 64, w: 73},
    fish: {h: 118, w: 86}
  })

  export function fish(id: number) {
    return "fish_" + id
  }

  export function item(id: number) {
    return "item_" + id
  }

  export function book(id: number) {
    return "book_" + id
  }

  export function cat(id: number) {
    return "cat_" + id
  }

  export function flow(id: number) {
    return "flow_" + id
  }

  export function butt(id: number) {
    return "butt_" + id
  }

  export function fish_jump(id: number) {
    return "fish_jump_" + id
  }

  export function toktok(id: number) {
    return "toktok_" + id
  }

  export const arrow = "arrow"
  export const light = "light"
  export const day = "day"
  export const bgLeft = "bg_left"
  export const night = "night"
  export const picture = "picture"
  export const pictureDark = "picture_dark"
  export const toktokDark = "toktok_dark"

  const catList = _.range(catCount).map(cat)
  const flowList = _.range(flowCount).map(flow)
  const fishList = _.range(fishCount).map(fish)
  export const fishJumpList = _.range(fishJumpCount).map(fish_jump)
  const itemList = _.range(itemCount).map(item)
  const bookList = _.range(bookCount).map(book)
  export const toktokList = _.range(toktokCount).map(toktok)
  export const buttList = _.range(buttCount).map(butt)
  export const keys = [
    bgLeft,
    arrow,
    light,
    toktokDark,
    day,
    night,
    picture,
    pictureDark
  ].concat(
    catList,
    buttList,
    fishList,
    flowList,
    fishJumpList,
    itemList,
    bookList,
    toktokList
  )
  export const data: ImagesMetaData = {
    sprites: keys,
  }

}




