import {CoreFonts} from "core/assets"
import {translationService} from "core/services"
import {WithNumIndex} from "core/types"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Signals} from "core/ui/animations/signals"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn, ThreeSliceBtn} from "core/ui/controls"
import {screenSizeService} from "core/ui/services/instances"
import {tuple} from "core/utils"
import {Numbers} from "core/utils/numbers"
import {AnswersComponent} from "games/shop/components/answersComponent"
import {FishComponent} from "games/shop/components/fishComponent"
import {RoomClockComponent} from "games/shop/components/roomClockComponent"
import {RoomComponent} from "games/shop/components/roomComponent"
import * as Dark from "games/shop/components/roomDarkComponent"
import {Images} from "games/shop/images"
import {Sounds} from "games/shop/sounds"
import {Translations} from "games/shop/translations"
import * as _ from "lodash"
import {Task} from '../logic'

const durations = {
  transition: 1000,
  toktok: 16,
  pictureStart: 400,
  picture: 4000,
  catSecond: 500,
  dark: 100,
  light: 1000,
  flash: 200,
  answers: 300
}

const tutorialText: WithNumIndex<string> = {
  0: Translations.title,
  2: Translations.subtitle
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    statusOffset: -150,
    shelfs: tuple(775, 216),
    buttonY: 1014,
    light: tuple(354, 409),
    answersY: 880,
    tutorialMinHeight: 30,
    fish: [1475, 260],
    arrow: [1590, 228],
    cat: [[484, 547], [970, 84], [1497, 549], [970, 84]],
    flow: tuple(819, 87)
  })

  positionsPortrait = Sizes.expand(this.positions, {
    cat: [[724, 92], [970, 84], [1260, 857], [900, 84]]
  })

  getPositions = () => screenSizeService.isPortrait ? this.positionsPortrait : this.positions
  tutorial: TutorialTextComponent
  private readyBtn: Btn
  private answers: AnswersComponent

  background = this.addImage([Images.day, [0, 0]])
  light = this.addImage([Images.light, this.positions.light])
  fish = this.add(this.createComponent(FishComponent)).setPos(this.positions.fish)
  toktok = this.addImage([Images.toktok(0), Dark.positions().toktok])

  backgroundDark = this.add(this.createComponent(Dark.RoomDarkComponent)).updateFields({alpha: 0})
  clock = this.add(this.createComponent(RoomClockComponent)).setPos(this.positions.arrow)
  shelfs = this.add(this.createComponent(RoomComponent))
    .setPos(this.positions.shelfs)
    .anchorCenterX()
  flow = this.addImage([Images.flow(0), this.positions.flow])
  picture = this.addImage([Images.picture, Dark.positions().picture]).anchorCenterX()
  cat = this.addImage([Images.cat(0), [0, 0]])
  catFrames = screenSizeService.isPortrait ? [0, 1, 3, 2] : [0, 1, 0, 2]

  pictureAnimation(reverse: boolean) {
    this.playSound(Sounds.picture_loop)
    const angle = 25 * (reverse ? 1 : -1)
    CommonAnimations.gravityAngle(this.picture, angle, durations.pictureStart, durations.picture)
  }

  onInit() {
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => this.playSound(Sounds.bg, true))
    this.statusOptions.offset = this.positions.statusOffset
    this.statusOptions.waitCompleteBeforeNewTask = true
    // this.toktok.img.animations.add("main", Images.toktokList.map(x => this.gameKey(x))).play(durations.toktok, true)

    this.readyBtn = this.add(new ThreeSliceBtn(
      _.upperFirst(translationService.getTranslation(Translations.remember)),
      Images.buttList.map(x => this.gameKey(x)),
      () => this.onReady(),
      CoreFonts.button("#3D7AAD")))
      .anchorCenterX()
      .updatePos(Sizes.w2, this.positions.buttonY)

    this.answers = this.add(this.createComponent(AnswersComponent))
    // .anchorCenterX()
      .updatePos(0, this.getPositions().answersY)
    this.setAnswerVisible(false)

    if (this.isTutorial) {
      this.tutorial = this.add(this.createComponent(TutorialTextComponent)).make()
    }
    this.updateState(0)

    this.startIntro()

  }

  showTutorial(state: number) {
    if (this.isTutorial) {
      const key = tutorialText[state]
      const text = key ? translationService.getTranslation(key) : ""
      this.tutorial.setProps(text)
    }
  }

  lightAnimation() {
    this.light.alpha = 0.3
    Tweens.start({
      target: this.light,
      to: {alpha: 1},
      duration: durations.light,
      easing: Phaser.Easing.Bounce.Out
    })
  }

  lightFlash() {
    this.light.alpha = 1
    Tweens.start({
      target: this.light,
      to: {alpha: 0.3},
      duration: durations.flash,
      simpleYoyo: true,
      repeat: 2
    })

  }

  updateCat(state: number) {
    const key = Numbers.odd(state) ? Sounds.mur_mur : Sounds.cat
    this.stopSound(Sounds.mur_mur)
    this.playSound(key)
    const p = this.getPositions()
    this.cat.key = this.gameKey(Images.cat(this.catFrames[state]))
    this.cat.setPos(p.cat[state])
  }

  updateState(state: number) {

    this.showTutorial(state)
    const p = this.getPositions()
    const isLight = Numbers.odd(state)

    if (!isLight) {
      this.shelfs.hideAllItems()
    }

    if (!this.isFirstTask) {
      const lightSound = isLight ? Sounds.light_off : Sounds.light_on
      this.playSound(lightSound)
    }

    if (isLight) {
      this.lightAnimation()
    }
    this.setAnswerVisible(state == 2)
    Tweens.start({
      target: this.backgroundDark,
      to: {alpha: isLight ? 0 : 1},
      duration: durations.dark
    })
    this.updateCat(state)
    this.flow.key = this.gameKey(Images.flow(state > 2 ? 1 : state))
    this.picture.key = this.gameKey(isLight ? Images.picture : Images.pictureDark)

    if (this.isInit && [1, 0].indexOf(state) >= 0) {
      this.pictureAnimation(isLight)
    }
  }

  changeItems(dark: boolean) {
    const [startState, endState] = dark ? [1, 2] : [3, 0]
    this.updateState(startState)

    const signal = this.delay(durations.transition)
    this.subscribe(signal, () => this.updateState(endState))
    return signal
  }

  setAnswerVisible(visible: boolean) {
    this.answers.visible = visible
    if (visible) {
      this.shelfs.hideMissingItems()
      Tweens.start({
        target: this.answers,
        to: {alpha: 1},
        duration: durations.answers
      })
    } else {
      this.answers.alpha = 0
    }

  }

  onReady() {
    this.readyBtn.visible = false
    return this.changeItems(true)
  }

  onAnswer(answer: ILastAnswer) {
    if (AnswerStatus.isCorrect(answer.status)) {
      this.lightFlash()
    }
  }

  onNewTask(task: Task) {
    const signal = this.isFirstTask
      ? Signals.memorize()
      : this.changeItems(false)
    return this.disableInputOn(signal).addOnce(() => {
      this.shelfs.setProps(task)
      this.answers.setProps(task.allAnswers)
      this.readyBtn.visible = true
    })
  }

}



