import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components"
import {OnInit} from "core/ui/components/onInit"
import {tuple} from "core/utils"
import {Images} from "games/shop/images"
import {Sounds} from "games/shop/sounds"

const durations = {
  animation: 2000,
  delay: 400,
  fish: 10
}

const maxSwimCount = 4

export class FishComponent extends GameComponent implements OnInit {
  positions = Sizes.create({
    fish: tuple(5, 75),
    right: 55
  })

  frameIndex = 0
  frames = [1, 2, 3, 0]
  fish = this.addImage([Images.fish(this.getNextFrame()), this.positions.fish])
  fishJump = this.addImage([Images.fish_jump(0), [0, 0]]).updateFields({visible: false})
  jump = this.fishJump.img.animations.add("main", Images.fishJumpList.map(x => this.gameKey(x)))
  swimCount = 0

  tween = Tweens.start({
    to: {x: this.positions.right},
    delay: durations.delay,
    simpleYoyoDelay: durations.delay,
    target: this.fish,
    duration: durations.animation,
    simpleYoyo: true,
    onLoop: () => {
      this.swimCount += 1
      this.fish.key = this.gameKey(Images.fish(this.getNextFrame()))
      this.delay(durations.delay).addOnce(() =>
        this.fish.key = this.gameKey(Images.fish(this.getNextFrame()))
      )
    },
    onUpdate: v => {
      if (v > 0.4 && this.swimCount == maxSwimCount && this.frameIndex == 1) {
        this.swimCount = 0
        this.playJump()
      }
    },
    repeat: -1
  })

  getNextFrame() {
    this.frameIndex = (this.frameIndex + 1) % this.frames.length
    return this.frames[this.frameIndex]
  }

  playJump() {
    this.playSound(Sounds.emerges_fish)
    this.fish.visible = false
    this.fishJump.visible = true
    this.tween.pause()
    this.jump.play(durations.fish).onComplete.addOnce(() => {
      this.fish.visible = true
      this.fishJump.visible = false
      this.tween.resume()
      this.playSound(Sounds.dive_fish)
    })
  }

  onInit() {

  }

}




