import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Control, Img} from "core/ui/controls"
import {Arrays} from "core/utils"
import {RoomShelfComponent} from "games/shop/components/roomShelfComponent"
import {shelfCount, Task} from "games/shop/logic"
import * as _ from "lodash"

export class RoomComponent extends GameComponent<Task> implements OnInit {
  positions = Sizes.create({
    marginTop: 8,
    shelfHeight: 107
  })

  shelfs: RoomShelfComponent[]
  images: Img[] = []

  shelfGroup = this.add(new Control())

  onInit() {
  }

  hideMissingItems() {
    this.shelfs.forEach(x => x.setVisibleForAllItems(true))
    this.shelfs.forEach(x => x.setVisibleForItems(this.props.missingItems, false))
  }

  hideAllItems() {
    this.shelfs.forEach(x => x.setVisibleForAllItems(false))
  }

  line(index: number, shelf: RoomShelfComponent) {
    const y = this.positions.marginTop + index * this.positions.shelfHeight
    shelf.updatePos(0, y)
  }

  setShelfPositions() {
    this.shelfs.forEach((x, i) => {
      this.line(i, x)
    })
  }

  createShelfs() {
    this.shelfGroup.removeAll(true)
    this.images.forEach(x => x.visible = true)
    const emptyShelfs = _.times(shelfCount - this.props.shelfs.length, () => ({items: [], length: 0}))
    const notEmptyPosition = emptyShelfs.length >= 2 ? 2 : emptyShelfs.length
    this.shelfs = Arrays.insert(emptyShelfs, notEmptyPosition, this.props.shelfs).map((shelf, index) =>
      this.createComponentWithProps(RoomShelfComponent, {...shelf, index}))
    this.setShelfPositions()
    this.shelfGroup.addChildren(_.flatten(this.shelfs))
  }

  onPropsUpdate() {
    this.createShelfs()
  }
}




