import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components"
import {OnInit} from "core/ui/components/onInit"
import {tuple} from "core/utils"
import {Images} from "games/shop/images"

export const positions = () => Sizes.create({
  right: 45,
  picture: tuple(500, 133),
  toktok: tuple(1127, 126)
})

export class RoomDarkComponent extends GameComponent implements OnInit {
  positions = positions()

  onInit() {
    this.addImage([Images.night, [0, 0]])
    this.addImage([Images.toktokDark, this.positions.toktok])
  }

}







