import {GameLogic, ITask} from "core/logic"
import {Arrays, RandomGenerator, tuple} from "core/utils"
import {Numbers} from "core/utils/numbers"
import * as _ from "lodash"
import {Level} from "./levels"

export const itemCount = 83
const minSheft = 2
export const shelfMaxPositionCount = 5
export const shelfCount = 6
const allItems = _.range(itemCount)

type ItemId = number

interface ItemPosition {
  position: number
  item: ItemId
}

export interface Shelf {
  items: ItemPosition[]
  length: number
}

export interface Task extends ITask {
  shelfs: Shelf[]
  missingItems: ItemId[]
  allAnswers: ItemId[]
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()

  createShelf(startIndex: number, max: number) {
    const maxShelf = Math.max(minSheft, max - minSheft)
    const length = this.rng.count(maxShelf, minSheft)
    const line = {startIndex, length: max}
    return line
  }

  createShelfs(positionCount: number) {
    const [count, mod] = Numbers.countAndMod(positionCount, shelfMaxPositionCount)
    const lineLengths = [..._.times(count, () => shelfMaxPositionCount), mod]
    return lineLengths.reduce(([l, len], v) => tuple([...l, this.createShelf(len, v)], len + v), tuple([], 0))[0]
  }

  itemPositions(items: number[], positionCount: number) {
    const itemPositions = Arrays.zip(items, _.take(this.rng.range(positionCount), items.length))

    function getItems({startIndex, length}: { startIndex: number, length: number }) {
      const endIndex = startIndex + length
      return itemPositions
        .filter(([item, position]) => position >= startIndex && position < endIndex)
        .map(([item, position]) => ({item: item, position: position - startIndex}))
    }

    const shelfLines = this.createShelfs(positionCount)
    return shelfLines.map(line => ({
      items: getItems(line),
      length: line.length
    }))
  }

  nextTask(level: Level) {
    const [initItems, wrongItems] = this.rng.split(allItems, level.itemCount)
    const [missingItems, questionItems] = this.rng.split(initItems, level.hiddenItemCount)
    const answers = missingItems
    const allAnswers = this.rng.shuffle(answers.concat(_.take(wrongItems, level.wrongAnswerCount)))
    const shelfs = this.itemPositions(initItems, level.positionCount)
    const task = {
      shelfs,
      missingItems,
      allAnswers,
      answers
    }
    return task
  }

}





