import {ImageKey} from "core/types/index"
import {Img} from "core/ui/controls/index"
import {phaserService} from "core/ui/services/instances"

export class ImageWithShadow extends Img {

  constructor(key: ImageKey) {
    super(key)

    const shadow = this.add(phaserService.makeSprite(key))
    shadow.tint = 0x000000
    shadow.alpha = 0.4
    shadow.scale.set(1.05, 1.05)
    this.bringToTop(this.img)
    // this.img.x = (shadow.width - this.img.width)/2
    // this.img.y = (shadow.height - this.img.height)/2
  }
}



