import {GameComponent} from "core/ui/components"
import {OnInit} from "core/ui/components/onInit"
import {Images} from "games/shop/images"
import {Sounds} from "games/shop/sounds"

const durations = {
  tick: 2000
}

export class RoomClockComponent extends GameComponent implements OnInit {

  arrow = this.addImage([Images.arrow, [0, 0]]).updateAnchor(0.1, 0.5)

  onInit() {
    this.interval(() => {
      this.arrow.angle += 10
      this.playSound(Sounds.clock_loop)
    }, durations.tick)
  }

}




